import React, {Component} from 'react'
import modalCss from './TodoModal.module.css'

class TodoModal extends Component {

    myRef = React.createRef()
    maxLength = 100
    minLength = 5

    editTextValue = ''

    componentDidMount() {
        if (this.props.modalState.type === 'edit') {
            this.myRef.current.innerHTML = this.maxLength - this.props.modalState.editText.toString().trim().length
            this.editTextValue = this.props.modalState.editText.toString().trim()
        }
    }

    changeTextHandler = (event) => {
        const value = event.nativeEvent.target.value.toString().trim()

        this.editTextValue = value
        this.myRef.current.innerHTML = this.maxLength - value.length
    }

    delete = () => {
        this.props.deleteHandler(this.props.modalState.deleteId)
        this.props.closeHandler()
    }

    edit = () => {
        const length = this.editTextValue.length

        if (length < this.minLength || length > this.maxLength) {
            alert(`Invalid string length! The correct length is ${this.minLength} to ${this.maxLength} characters.`)
            return
        }

        if (this.props.modalState.editText.toString().trim().length !== this.editTextValue.length) {
            this.props.editHandler(this.props.modalState.editId, this.editTextValue)
        }

        this.props.closeHandler()
    }

    render() {
        console.log(this.props)

        return (
            <React.Fragment>
                {
                    this.props.modalState.isShow && this.props.modalState.type === 'delete'
                        ? <div className={modalCss.container}>
                            <div className={modalCss.delete}>
                                <h2 className={modalCss.delete__title}>Delete item?</h2>
                                <div className={modalCss.delete__buttons}>
                                    <button
                                        className={modalCss.delete__button}
                                        onClick={this.delete}
                                    >YES
                                    </button>
                                    <button
                                        className={`${modalCss.delete__button} ${modalCss.delete__buttons_no}`}
                                        onClick={this.props.closeHandler}
                                    >NO
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }

                {
                    this.props.modalState.isShow && this.props.modalState.type === 'edit'
                        ? <div className={modalCss.container}>
                            <div className={modalCss.edit}>
                                <h2 className={modalCss.edit__title}>Edit text</h2>
                                <textarea
                                    className={modalCss.edit__area}
                                    defaultValue={this.props.modalState.editText}
                                    onChange={this.changeTextHandler}
                                />
                                <span>
                                Characters limit:
                                    <p className={modalCss.edit__count} ref={this.myRef}/>
                            </span>
                                <div className={modalCss.edit__buttons}>
                                    <button
                                        className={modalCss.edit__button + ' ' + modalCss.edit__button_cancel}
                                        onClick={this.props.closeHandler}
                                    >CANCEL
                                    </button>
                                    <button
                                        className={modalCss.edit__button}
                                        onClick={this.edit}
                                    >SAVE
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }
            </React.Fragment>
        )
    }
}

export default TodoModal
