import React, {Component} from 'react'
import todosCss from './Todos.module.css'

import Todo from "./Todo/Todo";
import Filters from './Filters/Filters'
import Add from './Add/Add'
import Rebuild from "../Rebuild/Rebuild";

import {getAllTodos, saveTodos} from '../modules/todo'
import {isAuth} from '../modules/login'
import TodoModal from "./TodoModal/TodoModal";

class Todos extends Component {

    state = {
        todos: [],
        modal: {
            isShow: false,
            type: '',
            deleteId: null,
            editId: null,
            editText: ''
        }
    }

    sort = {
        priority: true,
        date: false,
        completed: false
    }

    searchToggle = true
    minLength = 5
    maxLength = 100

    componentDidMount() {
        if (!isAuth()) {
            this.props.history.push({
                pathname: '/'
            })

            return
        }

        getAllTodos().then(res => {
            const arr = res.map(el => {
                return {
                    ...el,
                    date: el.date || Date.now(),
                    priority: el.priority || el.userId,
                    hide: false
                }
            })

            saveTodos(arr)

            this.setState(() => {
                return {
                    todos: arr
                }
            })
        })
    }

    closeAlert = () => {
        this.setState(() => {
            return {
                modal: {
                    isShow: false,
                    type: '',
                    deleteId: null,
                    editId: null,
                    editText: ''
                }
            }
        })
    }

    showDeleteAlert = (id) => {
        if (!id) return

        this.setState(state => {
            return {
                modal: {
                    ...state.modal,
                    isShow: true,
                    type: 'delete',
                    deleteId: id
                }
            }
        })
    }

    showEditAlert = (id, title) => {
        if (!id || !title) return

        this.setState(state => {
            return {
                modal: {
                    ...state.modal,
                    isShow: true,
                    type: 'edit',
                    editId: id,
                    editText: title
                }
            }
        })
    }

    changePriority = (id, num) => {
        if (!id || !num) return

        const todos = this.state.todos.map(el => {
            if (el.id === id) {
                const result = el.priority + num
                el.priority = result > 1 ? result : 1
            }
            return el
        })

        if (todos.length) {
            this.saveAllTodosInStorage(todos)

            this.setState(() => {
                return {todos}
            })
        }
    }

    deleteTodo = (id) => {
        if (!id) return

        const todos = this.state.todos.filter(el => el.id !== id)

        this.saveAllTodosInStorage(todos)

        this.setState(() => {
            return {todos}
        })
    }

    editTodo = (id, title) => {
        if (!id || !title) return

        const todos = this.state.todos.map(el => {
            if (el.id === id) el.title = title

            return el
        })

        if (todos.length) {
            this.saveAllTodosInStorage(todos)

            this.setState(() => {
                return {todos}
            })
        }
    }

    checkedTodo = (id) => {
        if (!id) return

        // eslint-disable-next-line array-callback-return
        const todos = this.state.todos.map(el => {
            if (el.id === id) el.completed = !el.completed
            return el
        })

        if (todos.length) {
            this.saveAllTodosInStorage(todos)

            this.setState(() => {
                return {todos}
            })
        }
    }

    sortTodos = (key) => {
        let todos = []

        if (this.sort[key]) {
            todos = this.state.todos.sort((a, b) => +a[key] < +b[key] ? 1 : -1)
        } else {
            todos = this.state.todos.sort((a, b) => +a[key] > +b[key] ? 1 : -1)
        }

        if (todos.length) {
            this.sort[key] = !this.sort[key]

            this.setState(() => {
                return {
                    todos
                }
            })
        }
    }

    sortByPriority = () => {
        const KEY = 'priority'
        this.sortTodos(KEY)
    }

    sortByDate = () => {
        const KEY = 'date'
        this.sortTodos(KEY)
    }

    sortByChecked = () => {
        const KEY = 'completed'
        this.sortTodos(KEY)
    }

    searchHandler = (event) => {
        const TIMEOUT = 1000

        if (this.searchToggle) {
            this.searchToggle = false

            setTimeout(() => {
                const value = event.nativeEvent.target.value.toString().trim()
                this.searchAfterTimeout(value)

                this.searchToggle = true
            }, TIMEOUT)
        }
    }

    searchAfterTimeout = (value) => {
        let todos = []

        if (value) {
            todos = this.state.todos.filter(el => {
                el.hide = !el.title.includes(value)
                return el
            })
        } else {
            todos = this.state.todos.filter(el => {
                el.hide = false
                return el
            })
        }

        if (todos.length) {
            this.setState(() => {
                return {
                    todos
                }
            })
        }
    }

    addNewTodo = (value) => {
        const str = value.toString().trim().length

        if (str < this.minLength || str > this.maxLength) {
            alert(`Invalid string length! The correct length is ${this.minLength} to ${this.maxLength} characters.`)
            return
        }

        let maxId = 0

        this.state.todos.forEach(el => {
            if (el.id > maxId) maxId = el.id
        })

        const todo = {
            completed: false,
            date: Date.now(),
            hide: false,
            priority: 1,
            id: maxId + 1,
            title: value.toString().trim()
        }

        const todos = [todo, ...this.state.todos]

        if (todos.length) {
            this.saveAllTodosInStorage(todos)

            this.setState(() => {
                return {
                    todos
                }
            })
        }

    }

    saveAllTodosInStorage = async todos => {
        const newTodos = todos.map(el => {
            el.hide = false
            return el
        })

        if (newTodos.length) {
            saveTodos(newTodos)
        }
    }

    render() {
        return (
            <React.Fragment>
                {
                    this.state.modal.isShow
                        ? <TodoModal
                            modalState={this.state.modal}
                            deleteHandler={this.deleteTodo}
                            editHandler={this.editTodo}
                            closeHandler={this.closeAlert}
                        />
                        : null
                }

                <div className={todosCss.mainContainer}>
                    <h1 className={todosCss.title}>TODO LIST</h1>
                    <Add
                        addTodoHandler={this.addNewTodo}
                    />
                    <Filters
                        prioritySortHandler={this.sortByPriority}
                        dateSortHandler={this.sortByDate}
                        checkedSortHandler={this.sortByChecked}
                        searchHandler={this.searchHandler}
                    />
                    <Rebuild
                        history={this.props.history}
                    />
                    <div className={todosCss.Todos}>
                        {this.state.todos.length ?
                            this.state.todos.map(el => {
                                return (
                                    <Todo
                                        key={el.id}
                                        title={el.title}
                                        completed={el.completed}
                                        priority={el.priority}
                                        deleteHandler={this.showDeleteAlert.bind(this, el.id)}
                                        editHandler={this.showEditAlert.bind(this, el.id, el.title)}
                                        checkHandler={this.checkedTodo.bind(this, el.id)}
                                        upPriorityHandler={this.changePriority.bind(this, el.id, 1)}
                                        downPriorityHandler={this.changePriority.bind(this, el.id, -1)}
                                        hide={el.hide}
                                        date={el.date}
                                    />
                                )
                            })
                            : (
                                <div className={todosCss.loader}>
                                    <p>Loading...</p>
                                </div>
                            )
                        }
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Todos
