import React, {Component} from 'react'
import loginCss from './Auth.module.css'
import {isAuth, login, register} from '../modules/login'
import Login from "./Login/Login";
import Register from "./Register/Register";

class Auth extends Component {

    state = {
        activeForm: 'login'
    }

    componentDidMount() {
        if (isAuth()) {
            this.props.history.push({
                pathname: '/todos'
            })
        }
    }

    changeActiveForm = (type) => {
        this.setState({
            activeForm: type
        })
    }

    loginHandler = (values) => {
        if (!values) return

        const user = {
            email: values.email.toString().trim(),
            password: values.password.toString().trim()
        }

        const status = login(user)

        if (status) {
            this.props.history.push({
                pathname: '/todos'
            })
        } else {
            alert('User not found!')
        }

    }

    registerHandler = (values) => {
        if (!values) return

        const newUser = {
            email: values.email.toString().trim(),
            password: values.password.toString().trim(),
            passwordConfirm: values.passwordConfirm.toString().trim(),
        }

        const status = register(newUser)

        if (status) this.props.history.push({
            pathname: '/todos'
        })
    }

    render() {

        const legend = (
            <legend>
                     <span
                         className={this.state.activeForm === 'login' ? loginCss.active : null}
                         onClick={this.changeActiveForm.bind(this, 'login')}
                     >Login</span>
                <span
                    className={this.state.activeForm === 'reg' ? loginCss.active : null}
                    onClick={this.changeActiveForm.bind(this, 'reg')}
                >Register</span>
            </legend>
        )

        return (
            <div className={loginCss.LoginContainer}>
                <div className={loginCss.Login}>
                    {
                        this.state.activeForm === 'login'
                            ? <Login legend={legend} loginHandler={this.loginHandler}/>
                            : <Register legend={legend} registerHandler={this.registerHandler}/>
                    }
                </div>
            </div>
        )
    }
}

export default Auth
